.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item-description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expense-item-price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #383838;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #383838;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
}

.expense-date-month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date-year {
  font-size: 0.75rem;
}

.expense-date-day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item-delete {
  cursor: pointer;
  width: 4rem;
  height: 2.8rem;
  font-weight: bold;
  font-size: 1rem;
  color: red;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  border: 1px solid #ececec;
}

.expense-item-delete:hover {
  color: white;
  background-color: red;
}

@media (min-width: 580px) {
  .expense-item-description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item-description h2 {
    font-size: 1.25rem;
  }

  .expense-item-price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
